<template>
    <div class="main">
        <div class="main__search-in">
            <div class="main__search">
                <input type="text" class="main__search-input" v-model="search" placeholder="Введите имя пользователя"
                    @keydown.enter="sendInfo">
                <button class="main__search-button">НАЙТИ</button>

            </div>
            <div class="main__search-profile">
                <div class="main__search-profile-left">
                    <img :src="user.avatar_url" alt="" class="main__search-profile-left-img">
                    <a :href="user.url" class="main__search-profile-left-visit">Посетить</a>
                </div>
                <ul class="main__search-profile-right">
                    <li class="main__search-profile-right-name">{{ user.login }}</li>
                    <li class="main__search-profile-right-name">Репозиториев: {{ user.public_repos }}</li>
                    <li class="main__search-profile-right-name">Создан: {{user.created_at }}</li>
                    <li class="main__search-profile-right-name">Подписщиков: {{ user.followers }}</li>
                    <li class="main__search-profile-right-name">Подписок: {{ user.following }}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
export default {

    data() {
        return {
            search: '',
            name: '',
        }
    },
    computed: {
        ...mapState(['user']),
    },
    methods: {
        ...mapActions(['getUser']),
        sendInfo() {
            this.getUser(this.search)
        }

    }
}

</script>